.Onpoint {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: scroll;
  scroll-snap-type: y mandatory;
  background-attachment: local;
  background-size: contain;
}
.Onpoint section {
  height: 100vh;
  scroll-snap-align: center;
  position: relative;
}
.Background {
  position: absolute;
  width: 100%;
  background-attachment: local;
  background-size: contain;
}
